"use client";
import { Doughnut } from "react-chartjs-2";
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from "chart.js";

ChartJS.register(ArcElement, Tooltip, Legend);

export default function CategoryDonut({ byCategory }: { byCategory: Record<string, number> }) {
  const labels = Object.keys(byCategory);
  const values = Object.values(byCategory);

  const professionalPalette = [
    "#1f77b4", // blue
    "#ff7f0e", // orange
    "#2ca02c", // green
    "#d62728", // red
    "#9467bd", // purple
    "#8c564b", // brown
    "#e377c2", // pink
    "#7f7f7f", // grey
    "#bcbd22", // olive
    "#17becf", // teal
  ];

  const data = {
    labels,
    datasets: [
      {
        data: values,
        backgroundColor: professionalPalette.slice(0, labels.length),
        borderColor: "#ffffff",
        borderWidth: 1.5,
      },
    ],
  };

  const options = {
    responsive: true,
    maintainAspectRatio: false, // allows custom sizing
    cutout: "70%", // makes hole larger (default ~50%)
    plugins: {
      legend: {
        position: "top" as const,
        labels: {
          usePointStyle: true,
          color: "#374151", // grey-700
        },
      },
    },
  };

  return (
    <div className="max-w-sm mx-auto" style={{ height: 300, width: 300 }}>
      <Doughnut data={data} options={options} />
    </div>
  );
}
